/*
|----------------------------------------------
| RS SELECT
|----------------------------------------------
|
|
*/


.rs-select {
  display: block;
  width: auto;
  height: auto;
  min-width: 300px;
  max-width: 100%;
  position: relative;
}

.rs-select.is-disabled {
  color: #555555;
  background: #888888;
}

.rs-select-button {
  display: inline-block;
  width: 100%;
  border: 1px solid #333333;
  background: #888888;
  line-height: 40px;
  font-size: 20px;
  white-space: nowrap;
}

.rs-select-options {
  display: block;
  position: absolute;
  width: auto;
  min-width: 100%;
  height: auto;
  top: 100%;
  left: 0;
  z-index: 10;
  margin: 0;
  padding: 0;
  list-style: none;
}

.direction-up .rs-select-options {
  top: auto;
  bottom: 100%;
}

.rs-select-option {
  line-height: 26px;
  border: 1px solid red;
  border-top: none;
}

.rs-select-option.is-disabled {
  background: #888888;
  color: #555555;
}

.rs-select-option.is-selected {
  background: red;
  color: #ffffff;
}

.rs-select-indicator {
  &::after {
    content: "⇣";
  }
}
